export default {
  data: () => ({
    loading: true,
    buttonDisabled: false,
  }),
  methods: {
    async getEntity() {
      try {
        this.loading = true
        const response = await this.$store.dispatch('app/getItem', {
          entityName: this.entityName,
          id: this.getID,
        })
        this.formData = {...this.formData, ...response.data}
      } catch (e) {
        console.log(e)
      }
    },
    trimWhitespaces(object) {
      for (let i in object) {
        if (object[i] === String) object[i].trim()
      }
    },
    async saveEntity() {
      try {
        this.buttonDisabled = true
        this.trimWhitespaces(this.formData)
        const response = await this.$store.dispatch('app/saveEntity', {
          id: this.getID,
          data: this.formData,
          method: this.method,
          entityName: this.entityName
        })
        this.$notification.success({message: this.getMessage})
        this.$router.push(`/${this.entityName}/read/${response.id}`)
      } catch(e) {
        console.log(e)
        this.$errors.showOnce(e.response.data)
      } finally {
        this.buttonDisabled = false
      }
    },
    async deleteItem(id) {
      this.loading = true
      try {
        await this.$store.dispatch('app/deleteItem', {
          entityName: this.entityName,
          id
        })
        this.$notification.success({message: 'Успешно удалено'})
      } catch(e) {
        console.log(e)
        this.$errors.showOnce(e.response.data)
      } finally {
        this.loading = false
      }
    }
  },
  computed: {
    disabled() {
      return this.action === 'read'
    },
    getID() {
      if (this.id === '_') return
      return this.id
    },
    method() {
      switch (this.action) {
        case 'create': return 'post'
        case 'edit': return 'patch'	
        default: return null
      }
    },
    getButtonAction() {
      switch (this.action) {
        case 'create': return 'Создать'
        case 'read': return 'Редактировать'
        case 'edit': return 'Сохранить'	
      }
    },
    getAction() {
      switch (this.action) {
        case 'create': return 'Создание'
        case 'read': return 'Просмотр'
        case 'edit': return 'Редактирование'	
      }
    },
    getMessage() {
      switch (this.action) {
        case 'create': return 'Успешно создано'
        case 'edit': return 'Успешно сохранено'	
      }
    },
  }
}
